import React from 'react';
import PropTypes from 'prop-types';
import { Button, Modal } from 'react-bootstrap';

function ConfirmPrompt({ toggle, deleteRecipe }) {
  return (
    <Modal
      onHide={toggle}
    >
      <Modal.Body>
        <Modal.Title>Are you sure you want to delete this recipe?</Modal.Title>
        <Button bsStyle="primary" onClick={toggle}>Cancel</Button>
        <Button bsStyle="danger" onClick={deleteRecipe}>Delete</Button>
      </Modal.Body>
    </Modal>
  );
}

export default ConfirmPrompt;



